<template>
  <div class="statistics-page">
    <div style="margin: 10px 50px;">
      <span class="demonstration">选择开始日期：</span>
      <el-date-picker
        type="date"
        v-model="startTime"
        @change="getstatistics"
        placeholder="选择日期">
      </el-date-picker>
      <span style="margin-left: 20px;" class="demonstration">选择结束日期：</span>
      <el-date-picker
        type="date"
        v-model="enDTime"
        @change="getstatistics"
        placeholder="选择日期">
      </el-date-picker>
    </div>

    <el-tabs tab-position="left" @tab-click="tabClick" v-model="activeName">
      <el-tab-pane label="练习" name="2">
        <el-divider content-position="left">未完成练习统计</el-divider>
        <el-table :data="practiceList" border stripe>
          <el-table-column label="姓名" prop="name"></el-table-column>
          <el-table-column label="次数" prop="count"></el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="作业" name="1">
        <el-divider content-position="left">未完成作业统计</el-divider>
        <el-table :data="taskList" border stripe>
          <el-table-column label="姓名" prop="name"></el-table-column>
          <el-table-column label="次数" prop="count"></el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { getstatistics } from '@/api/statistics'
export default {
  data() {
    return {
      startTime: '',
      enDTime: '',
      activeName: '2',
      practiceList: [],
      taskList: []
    }
  },

  created() {

  },

  methods: {
    async getstatistics() {
      this.practiceList = []
      this.taskList = []
      if (this.startTime === '' || this.enDTime === '') return
      const res = await getstatistics({
        startTime: this.startTime,
        endTime: this.enDTime,
        classId: this.$store.state.classId,
        type: this.activeName - 0
      })

      if (this.activeName === '2') {
        this.practiceList = res.data.data
      } else {
        this.taskList = res.data.data
      }
    },
    tabClick() {
      this.getstatistics()
    }
  }
}
</script>

<style scoped lang='less'>
.statistics-page {
  padding: 15px;
}
</style>
